body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: monospace;
}

.App {
  text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.App-content {
    position: relative;
    padding: 20px;
    margin: 20px;
    width: calc(100% - 80px);
    z-index: 1000;
}

.search-bar {
    height: 100px;
    width: 100%;
}

.search-bar-container {
    width: 100%;
    border: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
    position: relative;
    max-height: 1000px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-size: cover;
    background-position: center center;
    margin: auto 0 80px;
}

.search {
    position: relative;
    border-radius: 8px;
    background-color: rgba(114, 66, 117, 0.5);
    margin-right: 16px;
    width: 70%;
}

.search:hover, .search:focus-within {
    background-color: rgba(114, 66, 117, 0.7);
}

.searchIcon {
    width: 36px;
    height: 100%;
    position: absolute;
    pointer-events: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.whiteSearch {
    color: white;
}

.inputRoot {
    color: inherit;
    width: calc(100% - 36px);
    padding-left: 36px;
    height: 42px;
}

.inputInput {
    padding-top: 8px;
    padding-right: 8px;
    padding-bottom: 8px;
    padding-left: 80px;
    width: 100%;
}
.image-center-cropped {
    object-fit: cover; /* Do not scale the image */
    padding-top: 100%;
    width: 100%;
}
